import Bmob from "hydrogen-js-sdk";
import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import loading from "@/components/Loading/index.vue"

let that;
let uid;
export default {
	components: {
		faIcon,
		loading
	},
	data() {
		return {
			loading: true,
			shop: "",
			staffs: [],
			reserve_num: 0,
			reserve_money: 0,
		}
	},

	onLoad() {
		that = this;
		uid = uni.getStorageSync("uid");
		that.shop = uni.getStorageSync("shop")
		that.getstaff_list()
	},
	methods: {
		show_options() {
			uni.showActionSheet({
				itemList: ["编辑", "删除"],
				success: function(res) {
					if (res.tapIndex == 0) {
						that.edit(that.shop)
					} else if (res.tapIndex == 1) {
						that.delete_this(that.shop.objectId)
					}
				},
				fail: function(res) {
					console.log(res.errMsg);
				}
			});
		},

		//得到员工列表
		getstaff_list: function() {
			const query = Bmob.Query("_User");
			query.order("-createdAt");
			query.equalTo("masterId", "==", uid);
			query.equalTo("shop", "==", that.shop.objectId);
			query.find().then(res => {
				//console.log(res)
				that.loading = false;
				that.staffs = res;
			});
		},

		//编辑操作
		edit(item) {
			uni.setStorageSync("warehouse", item);
			uni.setStorageSync("charge", item.charge);
			uni.setStorageSync("shop", item.shop);
			uni.navigateTo({
				url: "../add/add"
			})
		},

		//删除操作
		delete_this(id) {
			uni.showModal({
				title: '提示',
				content: '请谨慎删除，一旦删除，数据不能恢复，是否删除此仓库',
				success: function(res) {
					if (res.confirm) {
						console.log(id);
						that.delete_data(id)
					}
				}
			});
		},

		//删除数据
		delete_data(id) {
			console.log(id)
			const query = Bmob.Query("shops");
			query.destroy(id).then(res => {
				console.log(res)
				uni.showToast({
					title: "删除成功",
					icon: "none"
				})
				uni.navigateBack({
					delta: 1
				})
			}).catch(err => {
				console.log(err)
			})
		},

		goto_detail(good) {
			uni.setStorageSync("now_product", good);
			uni.navigateTo({
				url: "/pages/manage/good_det/good_det"
			})
		},
	}
}
